.card {
  box-shadow: 0 0 3px white;
  border-radius: 10px;
  background-color: white;
  margin: 10px;
  height: 20vh;
  border: none;
}
.header > input {
  margin-bottom: 5px;
  padding: 5px 0;
  font-size: 1.2rem;
}
.copydeleteCard {
  text-align: right;
  padding: 10px;
}
.card-trash-logo,
.card-copy-logo {
  width: 15px;
  margin-left: 15px;
  cursor: pointer;
}

.description {
  height: auto;
  margin-top: 20px;
  color: rgb(120, 120, 120);
}
.cardName {
  border-radius: 5px;
  color: white;
  box-shadow: 0 0 3px rgb(120, 120, 120);
}
.hide {
  display: none;
}

.card-copy-logo {
  width: 15px;
  cursor: pointer;
  margin-left: 15px;
}
