input,
textarea {
  width: 90%;
  text-align: center;
  border: none;
  resize: none;
  overflow: hidden;
}

.dashboard {
  margin: 10px;
  border-radius: 10px;
  border: none;
  padding-bottom: 5px;
}
.trash-create {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  margin-left: auto;
  border: none;
}
.create-card {
  background-color: transparent;
  color: white;
  border-radius: 3px;
  padding: 8px;
  position: relative;
  font-size: 0.9rem;
  border: none;
  cursor: pointer;
  margin: 3px;
}
.trash-logo {
  width: 15px;
  margin: 10px;
  cursor: pointer;
  filter: invert();
}

.dashboardName {
  background-color: white;
  color: rgb(120, 120, 120);
  font-weight: 700;
  font-size: 1.3rem;
  margin: 5px 0 10px 0;
  padding: 10px 0;
  border-radius: 4px;
  box-shadow: 0 0 5px orange;
}

.create-dashboard {
  width: 40px;
  background-color: transparent;
  opacity: 0.6;
  display: none;
}

.create-dashboard-icon {
  width: 40px;
  display: none;
}

.dashboard-wrapper:last-child > .create-dashboard-icon {
  display: inline;
  cursor: pointer;
}
